<!-- 角色设置 -->
<!-- 账号管理 -->

<template>
  <div>
    <top-title title="权限角色设置"></top-title>
    <div class="fu">
      <div class="topBtnWrap">
        <div>
          <Button class="btnColor" icon="plus" type="primary" @click='addRole'>新建角色</Button>
        </div>
        <div>
          <Input v-model="roleNameLike" placeholder="请输入角色名称" style="width: 300px"></Input>
          <Button class="btnColor" icon="search" @click='search'>搜索</Button>
        </div>
      </div>


      <div>
        <Table style="margin-top:10px;" :loading="loading" border ref="selection" :columns="columns"
               :data="data1"></Table>
      </div>
      <Page @on-change='clickPage' :total="totalCount" show-total></Page>
    </div>

    <new-permissions-role ref="newPermissionsRole" v-on:filterParent="getFilterData"></new-permissions-role>
    <update-permissions-role ref="updatePermissionsRole" v-on:filterParent="getFilterData"></update-permissions-role>

  </div>
</template>
<script>
  import PageSize from '../../../common/PageSize'
  import topTitle from '@/components/common/topTitle'
  import newPermissionsRole from '@/components/pages/accountAndSettings/roleSetting/child/newPermissionsRole'
  import updatePermissionsRole from '@/components/pages/accountAndSettings/roleSetting/child/updatePermissionsRole'


  export default {
    components: {topTitle, newPermissionsRole, updatePermissionsRole},
    data() {
      return {
        roleNameLike: '',
        loading: true,
        pageSize: 10,
        pageSizeGroup: 10,
        roleArr: [],
        pageNo: 1,
        pageNoGroup: 1,
        totalCount: 0,
        totalCountGroup: 0,
        sysRoleId: '',
        modal1: false,
        bolOk: null,
        paramsIndex: '',

        isAdds: true,
        isDeletes: true,
        addCustomParams: [],
        deleteCustomParams: [],
        data1: [],
        columns: [
          {
            title: '权限角色',
            key: 'sysRoleName'
          },
          {
            title: '功能列表',
            key: 'dispResourceNames'
          },
          {
            title: '备注',
            key: 'description'
          },

          {
            title: '操作',
            key: 'operate',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#1fa2fd',
                    borderColor: '#1fa2fd'

                  },
                  on: {
                    click: () => {

                      this.$refs.updatePermissionsRole.beforeLoad(params.row.sysRoleId);
                      // this.$refs.updatePermissionsRole.getResource(params.row.sysRoleId);
                    }
                  }
                }, '修改'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  style: {

                    backgroundColor: '#ea0b00',
                    borderColor: '#ea0b00'
                  },
                  on: {
                    click: () => {
                      // this.modal1 = true;
                      // this.paramsIndex = params.index;
                      // this.sysRoleId = params.row.sysRoleId;

                      let v = [];
                      v.push(params.row);

                      this.deleteBox(v);
                    }
                  }
                }, '删除')
              ]);
            }

          }

        ],
      }
    },
    created() {
      this.roleList();
    },
    methods: {
      addRole: function () {
        this.$refs.newPermissionsRole.beforeLoad();
      },

      selectData: function (selection) {
        console.log(selection)
        let canDeletes = selection.filter((item) => {
          return item.status == 1;
        });

        let canAdds = selection.filter((item) => {
          return item.status == 0;
        });
        console.log(canAdds)
        this.isAdds = !canAdds.length > 0;
        alert(this.isAdds)
        this.addCustomParams = canAdds;
        this.isDeletes = !canDeletes.length > 0;
        this.deleteCustomParams = canDeletes;
      },

      deleteBox(params) {
        console.log(params);
        let sysRoleNames = params.map((item, index) => {
          return item.sysRoleName;
        });
        let sysRoleIds = params.map((item, index) => {
          return item.sysRoleId;
        });
        this.$Modal.confirm({
          title: '删除该角色',
          content: `你确定要删除` + sysRoleNames.join(","),
          onOk: () => {
            this.deleteMaterialData(sysRoleIds.join(","));
            console.log(sysRoleIds.join(","))
          }
        })
      },

      deleteMaterialData(sysRoleId) {
        const url = "/user/deleteSysRole.json?sysRoleId=" + sysRoleId;
        console.log(sysRoleId)
        this.$Http.get(url, {}).then(res => {
          console.log(res)
          this.$Message.info(res.body.data);
          this.roleList();


        })
      },


      show(index) {
        this.$Modal.info({
          title: 'User Info',
          content: `Name：${this.data1[index].name}<br>Age：${this.data1[index].age}<br>Address：${this.data1[index].address}`
        })
      },
      remove(index) {
        this.data1.splice(index, 1);
      },
      roleList() {
        this.data1 = [];

        const userName = sessionStorage.getItem('userName');
        const url = `/user/getSysRoleList.json?roleNameLike=${encodeURIComponent(this.roleNameLike)}&pageSize=${this.pageSize}&pageNo=${this.pageNo}`
        this.$Http.get(url, {}).then(res => {
          this.loading = false;
          console.log(res.body.data.result)
          this.roleArr = res.body.data.result;
          this.totalCount = res.body.data.totalCount;
          let roleArrList = [];
          for (var key in this.roleArr) {
            roleArrList = {
              'sysRoleName': this.roleArr[key].sysRoleName,
              'dispResourceNames': this.roleArr[key].dispResourceNames,
              'description': this.roleArr[key].description,
              'sysRoleId': this.roleArr[key].sysRoleId,
              'status': this.roleArr[key].status
            }
            this.data1.push(roleArrList);
          }
          console.log(this.data1)
          // this.data.data.userName=res.body.data.sysUserList.userName;
          // this.data.data.rolePermission=res.body.data.sysUserList.roles.name;
          // this.data.data.remark="";
        })
      },

      clickPage(data) {
        console.log(data);
        this.loading = true;
        this.pageNo = data;
        this.roleList();
      },
      search() {
        // alert(this.value)
        this.pageNo = 1;
        this.roleList()
      },
      handleCancel() {
        this.value = "";
        this.roleList()
      },
      ok() {

        const url = "/user/deleteSysRole.json?sysRoleId=" + this.sysRoleId;
        console.log(url);
        this.$Http.get(url, {}).then(res => {
          console.log(res);
          if (res.body.status == 0) {
            this.$Message.info(res.body.msg);
            this.data1.splice(this.paramsIndex, 1);
          } else if (res.body.status == 200) {
            this.$router.push({name: 'login'});
            this.$Message.info(res.body.msg);
          } else {
            this.$Message.info(res.body.msg);
          }


        })
      },
      cancel() {
        this.$Message.info('已取消删除');
      },
      getFilterData() {
        this.roleList();
      }


    }
  }
</script>

<style scoped>
  .ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 0px;
  }

  .topBtnWrap {
    display: flex;
    justify-content: space-between;
  }
</style>



