<template>
  <div>
    <Modal
      v-model="modal"
      title="新建角色" :styles="{width:'100%'}" class-name="modal-box">

      <div class="master-content">
        <div class="row">
          <label>角色名称:</label>
          <Input v-model="roleName" type="text" name="roleName" placeholder="" @on-blur='userDataNum'
                 @on-focus='onFocusName' style="width: 300px"></Input>
          <span :class="{'label-right-active':isAtive}" class="label-right">{{soltNameLen}}</span>
        </div>
        <div class="row level-permission">
          <label>权限功能配置:</label>
          <div class="leve-top">
            <CheckboxGroup v-for='(item,index) in resourceList' :key="index" v-model="resourceJson">
              <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
                <Checkbox :label="item.id" @click.prevent.native="handleCheckAll(1,item.id)">{{item.name}}</Checkbox>
              </div>
              <div class="level-one">
                <CheckboxGroup v-for='(itemChild,idx) in item.childList' :key="idx" v-model="resourceJson">

                  <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
                    <Checkbox :label="itemChild.id" @click.prevent.native="handleCheckAll(2,itemChild.id,item.id)">
                      {{itemChild.name}}
                    </Checkbox>
                  </div>
                  <div class="level-two">
                    <CheckboxGroup class="iclus_discheck" style="display: inline-block;white-space: nowrap;" v-for='(itemChildChild,num) in itemChild.childList' :key="num"  v-model="resourceJson">
                      <Checkbox disabled :label="itemChildChild.id">{{itemChildChild.name}}</Checkbox>
                    </CheckboxGroup>
                  </div>
                </CheckboxGroup>
              </div>

            </CheckboxGroup>


          </div>
        </div>


        <div class="row"><label>备注:</label><textarea v-model="roleDesc" placeholder="选填"
                                                     style="width:300px;height:200px;"></textarea></div>

      </div>
      <div slot="footer">
        <Button type="text" @click="modal=false">取消</Button>
        <Button class="btnColor" type="primary" @click="createNewRole">新建角色</Button>
      </div>
    </Modal>
  </div>
</template>
<script>

  export default {
    data() {
      return {
        roleName: "",
        roleDesc: "",
        modal: false,
        resourceList: [],
        resourceJson: [],
        resourceMap: [],//一级二级目录唯一生产
        soltNameLen: '最多可输入100个字符',
        isAtive: false,

      }


    },
    methods: {
      beforeLoad: function () {
        this.resourceList = [];
        this.resourceJson = [];
        this.resourceMap = [];
        this.roleName = "";
        this.roleDesc = "";
        this.getResourceList();
        this.modal = true;

      },

      handleCheckAll: function (level, currentId, parentId) {
        if (level == 1) {
          if (this.resourceJson.includes(currentId)) {
            //此时要去除二级三级目录
            this.resourceJson.splice(this.resourceJson.findIndex(v => v == currentId), 1);
            let secondJson = this.resourceMap[currentId];
            secondJson.forEach((secondId) => {
              this.resourceJson.splice(this.resourceJson.findIndex(v => v == secondId), 1);
              let s = currentId + "-" + secondId;
              let thirdJson = this.resourceMap[s];
              thirdJson.forEach((thirdId) => {
                this.resourceJson.splice(this.resourceJson.findIndex(v => v == thirdId), 1);
              });
            });
          } else {
            //此时要添加所有的二级和三级目录
            this.resourceJson.push(currentId);
            let secondJson = this.resourceMap[currentId];

            secondJson.forEach((secondId) => {
              this.resourceJson.push(secondId);
              let s = currentId + "-" + secondId;
              let thirdJson = this.resourceMap[s];
              thirdJson.forEach((thirdId) => {
                this.resourceJson.push(thirdId);
              });
            });


          }


        }


        if (level == 2) {
          //处理二级权限，不仅要考虑一级勾选状况，还要考虑三级勾选状况
          let st = parentId + "-" + currentId;
          let thirdJson = this.resourceMap[st];
          //首先处理三级目录
          if (this.resourceJson.includes(currentId)) {

            //如果已经包含
            this.resourceJson.splice(this.resourceJson.findIndex(v => v == currentId), 1);

            //验证1及目录包含状况
            let secondJson = this.resourceMap[parentId];
            let isHave = false;
            this.resourceJson.forEach((value) => {
                if (secondJson.includes(value)) {
                  isHave = true;
                  return;

                }
              }
            );
            if (!isHave) {//如果没有包含二级目录
              this.resourceJson.splice(this.resourceJson.findIndex(v => v == parentId), 1);
            }//


            thirdJson.forEach((thirdId) => {
              this.resourceJson.splice(this.resourceJson.findIndex(v => v == thirdId), 1);//删除所有的三级目录

            });


          } else {
            //如果不包含
            //首先增加进来
            this.resourceJson.push(currentId);
            this.resourceJson.push(parentId);
            thirdJson.forEach((thirdId) => {
              this.resourceJson.push(thirdId);
            });
          }

        }
        this.resourceJson = Array.from(new Set(this.resourceJson));
      },
      createNewRole: function () {
        this.resourceJson = Array.from(new Set(this.resourceJson));
        let resourceIds = this.resourceJson.join(",");
        console.log(resourceIds);
        let url = "/user/createSysRole.json?sysRoleName=" + encodeURIComponent(this.roleName)
          + "&resourceIds=" + resourceIds + "&description=" + encodeURIComponent(this.roleDesc);
        this.$Http.get(url, {}).then(res => {
          if (res.body.status == 0) {
            this.modal = false;
            this.$emit("filterParent");
          }
        });

      },
      getResourceList: function () {
        let url = "/common/getResourceList.json?level=1";
        this.$Http.get(url, {}).then(res => {
          if (res.body.status == 0) {
            let resourceList = res.body.data.resourceList;

            this.resourceList = resourceList;
            this.generateResourceMap();
            // this.modal=false;
            // this.$emit("filterParent");
          }
        });

      },
      generateResourceMap: function () {
        var m = {};
        this.resourceList.forEach((item) => {


          let id = item.id;
          this.resourceJson.push(id);
          let childList = item.childList;
          let c = [];
          childList.forEach((itemChild) => {
            let childId = itemChild.id;
            this.resourceJson.push(childId);
            c.push(childId);
            let childChildList = itemChild.childList;
            let cid = id + "-" + childId;
            let cc = [];
            childChildList.forEach((itemChildChild) => {
              cc.push(itemChildChild.id);
              this.resourceJson.push(itemChildChild.id);

            });
            m[cid] = cc;

          });
          m[id] = c;
        });
        this.resourceMap = m;
        console.log(m);


      },
      onFocusName() {
        this.soltNameLen = "最多可输入100个字符";
        this.isAtive = false;

      },
      userDataNum() {
        //姓名位数不在0-100间
        if (this.roleName) {
          var reg = /^[\s\S]{1,100}$/;
          var r = this.roleName.match(reg);
          if (r == null) {
            this.soltNameLen = "权限角色名长度超过一百个字符"
            this.isAtive = true;
          }
        } else {
          this.soltNameLen = "权限角色名不能为空"
          this.isAtive = true;
        }
      },


    }

  }

</script>

<style scoped>

  .master-content .row.level-permission {
    align-items: flex-start;
  }

  .master-content .row.level-permission label {
    width: 180px;
    display: inline;
  }

  .leve-top .level-one {
    margin: 10px 20px;

  }

  .leve-top .level-one .level-two {
    margin: 10px 20px;
    /*display: -webkit-flex; */
    /*display: flex;*/
    /*justify-content: flex-start;*/
  }

  .label-right-active {
    color: red !important;
  }

  .label-right {
    display: inline-block;
    margin-left: 20px;
  }
</style>



